import React from 'react';
import Rellax from 'react-rellax';
import './Features.css';
import Main from '../Main/Main';

const rocketImages = {
	'Falcon 1': 'falcon-1',
	'Falcon 9': 'falcon-9',
	'Falcon Heavy': 'falcon-heavy',
	'Starship': 'starship'
}

const Features = ({ features }) => (
  <>
		<Main rocket={features.name} />
		<section className="features">
			<h2 className="features-title">
				{features.name.toUpperCase()} <br/>Overview
			</h2>
			<div className="overview">
				<table className="table">
					<caption className="table-title">
						Size
					</caption>
					<thead>
						<tr>
							<td className="table-column">HEIGHT</td>
							<td className="table-column">{features.height.meters} m / {features.height.feet} ft</td>
						</tr>
						<tr>
							<td className="table-column">DIAMETER</td>
							<td className="table-column">{features.diameter.meters} m / {features.diameter.feet} ft</td>
						</tr>
						<tr>
							<td className="table-column">MASS</td>
							<td className="table-column">{features.mass.kg} kg / {features.mass.lb} lb</td>
						</tr>
						{features.payload_weights.map((item, i) => (
							<tr key={i}>
								<td className="table-column">PAYLOAD TO {item.id.toUpperCase()}</td>
								<td className="table-column">{item.kg} kg / {item.lb} lb</td>
							</tr>
						))}
					</thead>
				</table>
				<Rellax speed={14}>
					<img
						src={`./spaceX/img/${rocketImages[features.name]}.png`}
						alt="rocket"
						className="rocket"
					/>
				</Rellax>
				<article>
					<h3 className="features-subtitle">DESCRIPTION</h3>
					<p className="features-text">
						{features.description}
					</p>
				</article>
			</div>
		</section>
	</>
)

export default Features;